import { mapState } from "vuex";

export default {
    data() {
        return {
            show: false,
            cash_detail: '',
            setting: {},
            detail: {}
        }
    },
    computed: {
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
        })
    },
    methods: {
        toCash() {
            uni.navigateTo({
                url: '/plugins/stock/cash/cash'
            });
        },
        toDetail() {
            let name = this.cash_detail ? this.cash_detail : '';
            uni.navigateTo({
                url: '/plugins/stock/cash-detail/cash-detail?name=' + name
            });
        },
        getSetting() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.stock.setting,
            }).then(response=>{
                that.getInfo();
                if(response.code == 0) {
                    that.setting = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        getInfo() {
            let that = this;
            that.$request({
                url: that.$api.stock.info,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.detail = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        this.getSetting();
        if(options.name) {
            uni.setNavigationBarTitle({
                title: options.name,
            })
        }
        if(options.cash_detail) {
            this.cash_detail = options.cash_detail;
        }
    }
}
